<template>
  <div style="font-family: 'Quicksand', sans-serif;">
    <div class="py-4 px-2 shadow bg-blue-light text-white font-bold">
      <p class="mt-1 font-semibold text-xs float-right mr-3">
        Group Karyawan
      </p>
      <div @click="$router.go(-1)" class="ml-2 cursor-pointer">
          <svg  width="20" height="20" style="fill: white;" class="d-inline mr-1" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 320 512"><!--!Font Awesome Free 6.5.1 by @fontawesome - https://fontawesome.com License - https://fontawesome.com/license/free Copyright 2024 Fonticons, Inc.--><path d="M9.4 233.4c-12.5 12.5-12.5 32.8 0 45.3l192 192c12.5 12.5 32.8 12.5 45.3 0s12.5-32.8 0-45.3L77.3 256 246.6 86.6c12.5-12.5 12.5-32.8 0-45.3s-32.8-12.5-45.3 0l-192 192z"/></svg>
      </div>
    </div>
    <hr class="my-2">
    <div class="text-right">
      <button type="button" name="" id="" class="btn btn-sm btn-outline-primary  mr-3" @click="updateData()">Update Data</button>
      <button type="button" name="" id="" class="btn btn-sm btn-outline-primary  mr-3" v-if="$store.state.employees.length>1" @click="$router.push('/absen/group_selfie')">Group Absen </button>
    </div>
    <div class="row justify-content-center">
        <div class="col-11 shadow rounded-lg" style="font-family: 'Quicksand', sans-serif;">
            <div v-for="(item, index) in $store.state.employees" :key="index" class="p-1" @click="changeUser(item)">
                <div class="shadow rounded-lg p-3 row hover:bg-blue-400 cursor-pointer" >
                    <div class="col-3">
                        <img :src="'data:image/png;base64,'+item.photo" alt="">
                    </div>
                    <div class="col-9">
                        <p class="text-xs font-semibold">{{item.name}}</p>
                        <p class="text-xs font-semibold">Location : {{item.work_location}}</p>
                        <p class="text-xs font-semibold">Face data : {{JSON.parse(item.faces  ).length}}</p>
                    </div>
                </div>
            </div>
        </div>
    </div>
  </div>
</template>
<script>
import axios from 'axios';
import autophp from '@/plugins/auto';
let sdb = new autophp('ci');
export default {
  data() {
    return {
    }
  },
  methods: {
    async updateData(){
      let that=this;
      that.$store.dispatch('update_employees')
      setTimeout(() => {
        that.$router.push('/main')
      }, 1000);
    },
    changeUser(item){
      this.$store.state.hit_id=item['hit_id']
      this.$store.state.employee=item
      this.$router.push('/main')
    }
  },
  mounted() {
  }
};
</script>